// -----------------------------------------------------------------------
// convert slot start time and end time as
// (h:mm - h:mm A) e.x., (5:00 - 5:30 AM)
// -----------------------------------------------------------------------
export const slotTimeConverter = (startTime, endTime) => {
  return (
    moment(startTime, "HH:mm:ss").format("h:mm") +
    " - " +
    moment(endTime, "HH:mm:ss").format("h:mm A")
  );
};

