// -----------------------------------------------------------------------
export const slotTimeConverter12 = (startTime, endTime) => {
  return (
    moment(startTime, "HH:mm:ss").format("h:mm A") +
    " - " +
    moment(endTime, "HH:mm:ss").format("h:mm A")
  );
};

