// -----------------------------------------------------------------------
// checkIfTimeIsAfter()
// -----------------------------------------------------------------------
export const checkIfTimeIsAfter = (time1, time2) => {
  return (
    toTimeFormat(time1).replace(/:/g, "") >
    toTimeFormat(time2).replace(/:/g, "")
  );
};

// -----------------------------------------------------------------------
// checkIfTimeIsSameOrAfter()
// -----------------------------------------------------------------------
export const checkIfTimeIsSameOrAfter = (time1, time2) => {
  return (
    toTimeFormat(time1).replace(/:/g, "") >=
    toTimeFormat(time2).replace(/:/g, "")
  );
};

